<template>
  <div id="app">
    <navbar></navbar>
    <router-view></router-view>
  </div>
</template>

<script>
import hello from './components/Hello'
import navbar from './components/Navbar'
import store from './vuex/store'
import { mapActions } from 'vuex'


export default {
  name: 'app',
  components: {
    navbar,
    hello
  },
  store,
  methods: {
    ...mapActions([
      'postIndex',
      'getIndexData'
    ])
  },
  
  created() {
    this.postIndex()

  }

}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
}
li {list-style: none;}
a {text-decoration: none;color: #42bd56}
#app {
  width: 750px;
  margin: 0 auto;
}

.page {
    padding-top: 94px;
    overflow-x: hidden;
}

</style>



