                    </ul>
                </div>
            </section>
            <section class="movie_free_stream">
                <header><h2>免费在线观影</h2><a href="">更多</a></header>
                <div class="section-content"  v-if="show">
                    <ul class="row items">
                        <li class="item" v-for="item in getMovie.movie_free_stream" :key="item.id">
                            <movieItem :data="item"></movieItem>
                        </li>
                    </ul>
                </div>
            </section>
            <section class="movie_latest">
                <header><h2>新片速递</h2><a href="">更多</a></header>
                <div class="section-content"  v-if="show">
                    <ul class="row items">
                        <li class="item" v-for="item in getMovie.movie_latest" :key="item.id">
                            <movieItem :data="item"></movieItem>
                        </li>
                    </ul>
                </div>
            </section>
            <section class="interests">
                <header><h2>发现好电影</h2></header>
                <div class="section-content">
                    <ul>
                        <li style="border-color: #FF4055;"><a href="" style="color: #FF4055;">同时入选IMDB250和豆瓣电影250的电影</a></li>
                        <li style="border-color: #FFAC2D;"><a href="" style="color: #FFAC2D;">带你进入不正常的世界</a></li>
                        <li style="border-color: #4F9DED;"><a href="" style="color: #4F9DED;">用电【影】来祭奠逝去的岁月</a></li>
                        <li style="border-color: #CC3344;"><a href="" style="color: #CC3344;">女孩们的故事【电影】</a></li>
                        <li class="line"></li>
                        <li style="border-color: #3BA94D;"><a href="" style="color: #3BA94D;">科幻是未来的钥匙——科幻启示录【科幻题材】</a></li>
                        <li style="border-color: #2384E8;"><a href="" style="color: #2384E8;">美国生活面面观</a></li>
                        <li style="border-color: #FFC46C;"><a href="" style="color: #FFC46C;">2015终极期待</a></li>
