</template>
<script>
import movieItem from './movieItem'
import { mapGetters, mapActions } from 'vuex'
export default {
    name:'movie',
    data() {
        return {
            show:false
        }
    },
    components: {
        movieItem
    },
    methods: {
        ...mapActions([
            'postMovie',
            'getMovieData'
        ])
    },
    computed: {
        ...mapGetters([
            'getMovie'
        ])
    },
    mounted() {
        this.postMovie()
        
        this.$watch("getMovie.show", function(){
            this.show = true
        }, {deep:true})       
    }


}
</script>

<style scoped>
    section {
        overflow: hidden;
    }
    section header {
        padding: 0 40px;
        padding-top: 20px;
        background-color: #fff;
    }
    section h2 {
        display: inline-block;
        margin: 0 0 15px;
        font-size: 34px;
        font-weight: normal;
    }
    section header a {
        float: right;
        font-size: 28px;
        color: #42bd56;
        
    }
    section .section-content {
        margin-bottom: -40px;
    }
    .item {
        display: inline-block;
        vertical-align: top;
        width: 200px;
        text-align: center;
        margin-left: 18px;
    }
    .item:first-child {
        margin-left: 40px;
    }
    .item> * {
        font-size: 28px;
        white-space: normal;
    }
    .row {
        padding: 30px 0 86px 0;
    }
    .items {
        font-size: 0;
        white-space: nowrap;
        overflow: hidden;
        overflow-x: auto;
        
    }
    .items::-webkit-scrollbar { display: none }

    .interests ul {
