        overflow-x: auto;
        padding: 30px 30px 86px 30px;
        font-size: 0;
        margin-left: -10px;
    }
    .interests li {
        margin: 0 0 16px 16px;
        font-size: 36px;
        display: inline-block;
        border-radius: 18px;
        border:solid 1px;
        vertical-align: middle;
    }
    .interests li.line {
        width: 100%;
        display: block;
        height: 1px;
        border: 0;
        margin: 0;
    }
    .interests a {
        height: 100px;
        line-height:100px;
        padding: 0 112px;
        letter-spacing: 10px;
        overflow: auto;
        display: block;
        text-align: center; 
    }
    .types {
        margin-right: -50px;

    }
    .types .section-content {
        margin-left: 36px;
        margin-bottom: 0;
    }
    .type-list {
        padding: 20px 0 32px;
        color: #eee;
        font-size: 28px;
