        border-right: solid 1px #eee;
        float: left;
        padding-right: 40px;
        height: 95px;
    }
    .type-list li:nth-child(even) {
        border-right: none;
        padding-left: 40px;
    }
    .type-list a {
        width:300px;
        float: left;
        display: inline-block;
        line-height: 95px;
    }
    .type-list span {
        color: #eee;
        float: right;
        font-weight: bold;
        display: inline-block;
        border-right: solid 1px #ccc;
        border-bottom: solid 1px #ccc;
        width: 18px;
        height: 18px;
        transform: rotate(-45deg);
        margin-top: 37.5px;
    }
    .download-app {
        padding: 0 0 40px 0;
        margin-top: 100px;
        margin-bottom: 60px;
        text-align: center;
        font-size: 30px;
    }

    .download-app .info {
        margin: 0 auto 30px;
        overflow: hidden;
        text-align: left;
        font-size: 28px;
        display: inline-block;
        color: #111;
    }
    .download-app .info img{
        float: left;
        margin-right: 24px;
    }
    .download-app .info-content {
        overflow: hidden;
    }

    .download-app .info strong {
        font-size: 48px;
        font-weight: normal;
        line-height: 56px;
    }
    .download-app .info .info-content div {
        white-space: pre;
        line-height: 40px;
    }
    .download-app .info+a {
        display: block;
    }

</style>



